import React from 'react';

import classes from './ProgressBar.module.scss';

const ProgressBar = (props) => {

    let progress = {
        width: `${props.questionNumber * 10}%`,
        borderTopRightRadius: props.questionNumber === 10 ? '5px' : '0px',
        borderBottomRightRadius: props.questionNumber === 10 ? '5px' : '0px'
    };

    return (
        <div className={classes.ProgressBar}>
            <div className={classes.ProgressBar__Background}></div>
            <div className={classes.ProgressBar__Foreground} style={progress}></div>
        </div>
    )
}

export default ProgressBar;