import React from 'react';

import svg from '../../assets/image/sprite.svg';
import classes from './Timer.module.scss';

const Timer = (props) => {
    let classList = [classes.Timer];

    let icon = 'icon-hourglass-1';
    if(props.time <= 10 && props.time >= 5) {
        icon = 'icon-hourglass-2';
    }
    else if(props.time < 5) {
        icon = 'icon-hourglass-3';
        classList = [classes.Timer, classes.Blinking];
    }

    return (
        <div className={classList.join(' ')}>
            <svg className={classes.Timer__Icon}>
                <use xlinkHref={`${svg}#${icon}`}></use>
            </svg>
            <span className={classes.Timer__Text}>
                Time Left: {props.time}
            </span>
        </div>
    )
}

export default Timer;