import React, { useState, useCallback, useEffect, useRef } from 'react';
import he from 'he'; // To decode html entities
import axios from 'axios';

import classes from './Quiz.module.scss';
import Question from '../../components/Question/Question';
import ProgressBar from '../../components/ProgressBar/ProgressBar';
import UserScore from '../../components/UserScore/UserScore';
import Loader from '../../components/Loader/Loader';

const Quiz = (props) => {
    const [isLoading, setIsLoader] = useState(true);
    const [quizData, setQuizData] = useState([]);
    const [currQuestion, setCurrQuestion] = useState(0);
    const [answerCorrect, setAnswerCorrect] = useState(true);
    const [success, setSuccess] = useState(false);
    const [answered, setAnswered] = useState(false);
    const [userJoker, setUserJoker] = useState(1);
    const [userScore, setUserScore] = useState(0);

    let timeout;
    let loadingTimeout = useRef();

    const fetchData = useCallback(
        () => {
            setIsLoader(true);
            props.apiUrl && axios.get(props.apiUrl).then(result => {
                let questions = result.data.results.map(q => {
                    return {
                        ...q,
                        question: he.decode(q.question),
                        correct_answer: he.decode(q.correct_answer),
                        incorrect_answers: q.incorrect_answers.map(a => he.decode(a))
                    }
                });

                setQuizData(questions);
                loadingTimeout.current = setTimeout(() => {
                    setIsLoader(false);
                }, 2000); 
            }).catch(error => {
                console.log(error);
            });
        },
        [props.apiUrl]
    );

    useEffect(() => {
        fetchData();

        return () => {
            clearTimeout(timeout);
            clearTimeout(loadingTimeout.current);
        }
    }, [fetchData, timeout]);

    const onSelectedHandler = (questionNumber, selected, time) => {
        let isCorrect = answerCorrect && quizData[questionNumber - 1].correct_answer === selected;
        
        setAnswered(true);

        // Check if the answer is correct
        setAnswerCorrect(answerCorrect => isCorrect);

        if(!isCorrect) {
            timeout = setTimeout(() => {
                let gameOverData = {
                    header: 'Game Over',
                    text: 'You didn\'t manage to answer all the questions correctly!',
                    icon: 'close'
                }
                // setAnswered(false);
                onGameOver(gameOverData);
                
            }, 2000);
        }
        else {
            setUserScore(userScore => userScore + time);

            // Check if there are any questions left
            if(!(currQuestion < quizData.length - 1)) {
                setSuccess(true);
                timeout = setTimeout(() => {
                    let gameOverData = {
                        header: 'Congratulations!',
                        text: 'You managed to answer all the questions correctly!',
                        icon: 'check'
                    }
                    onGameOver(gameOverData);
                }, 2000);
            }
            else {
                setSuccess(true);
                timeout = setTimeout(() => {
                    if(currQuestion < quizData.length - 1) {
                        setCurrQuestion(currQuestion => currQuestion + 1);
                    }
                    setSuccess(false);
                    setAnswered(false);
                }, 2000);
            }
        }
    }

    const onJokerUsedHandler = (index) => {
        if(!(userJoker > 0)) {
            return;
        }

        setQuizData(quizData => {
            return quizData.map((question, i) => {
                if(index !== i) {
                    return question
                }
                else {
                    return {
                        ...question,
                        incorrect_answers: [question.incorrect_answers[Math.floor(Math.random(0, 1) * 3)]]
                    }
                }
            })
        });

        setUserJoker(0);
    }

    const onGameOver = (data) => {
        let newData = {
            ...data,
            score: userScore
        }

        props.gameOver(newData);
    }

    if(!props.apiUrl) {
        return null;
    }

    let pageContent = null;
    if(!isLoading) {
        pageContent = (
            <>
                <UserScore score={userScore} />
                <ProgressBar questionNumber={currQuestion + 1} />
                {quizData.length > 0 && currQuestion < quizData.length ? <Question 
                    questionNumber={currQuestion+1}
                    questionText={quizData[currQuestion].question}
                    questionChoices={quizData[currQuestion].incorrect_answers.concat(quizData[currQuestion].correct_answer)}
                    selected={onSelectedHandler}
                    timeIsUp={onGameOver}
                    success={success}
                    answered={answered}
                    jokerUsed={onJokerUsedHandler}
                    canUseJoker={userJoker > 0} /> : null}
            </>
        );
    }
    else {
        pageContent = <Loader />;
    }

    return (
        <div className={classes.Quiz}>
            {pageContent}
        </div>
    )
}

export default Quiz;