import React from 'react';

import classes from './Char.module.scss';

const Char = (props) => {

    let style = {
        transform: `rotateZ(${(Math.random() > 0.5 ? 1 : -1) * Math.random() * 10}deg)`
    }

    return (
        <span className={[classes.Char, classes[`Char__${props.number % 7}`]].join(' ')} style={style}>
            {props.letter}
        </span>
    )
}

export default Char;