import React from 'react';

import classes from './Options.module.scss';
import Option from './Option/Option';

const Options = (props) => {
    const optionSelectedHandler = (value) => {
        props.selectedOption(value);
    }

    let style = {};
    let options = null;
    if(props.show) {
        if(props.options.length > 5) {
            style = {
                overflowY: 'scroll',
                height: '14rem'
            };
        }

        options = (
            <ul className={classes.SelectOptions} style={style} ref={props.optionsRef}>
                {props.options.map(option => {
                    return <Option key={option} value={option} onOptionSelected={optionSelectedHandler}></Option>
                })}
            </ul>
        )
    }

    return (
        options
    )
}

export default Options;