import React, { useState, useRef, useEffect, useCallback } from 'react';

import classes from './SelectInput.module.scss';
import Options from './Options/Options';

const SelectInput = (props) => {
    const [showOptions, setShowOptions] = useState(false);
    const [selected, setSelected] = useState(props.itemList[0]);

    let optionsRef = useRef(null);
    let buttonRef = useRef(null);

    const handleClickOutside = useCallback(
        (event) => {
            if((optionsRef.current && buttonRef.current.contains(event.target))) {
                return;
            }

            if(!(optionsRef.current && optionsRef.current.contains(event.target))) {
                if(showOptions) {
                    setShowOptions(showOptions => !showOptions);
                }
            }
        },
        [showOptions],
    );

    useEffect(() => {
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        }
        
    }, [showOptions, handleClickOutside]);

    const onSelectInputClickedHandler = () => {
        setShowOptions(showOptions => !showOptions);
    }

    const onSelectOptionHandler = (value) => {
        setSelected(value);
        setShowOptions(showOptions => !showOptions);
        props.valueSelected(value);
    }
    
    let classList = [classes.SelectInput];
    if(showOptions) {
        classList = [classes.SelectInput, classes.SelectInput__OptionsShown];
    }
    else {
        classList = [classes.SelectInput];
    }

    return (
        <>
            <div className={classes.SelectInputWrapper}>
                <Options show={showOptions} options={props.itemList} selectedOption={onSelectOptionHandler} optionsRef={optionsRef} />
                <label>{props.label}</label>
                <div className={classList.join(' ')} onClick={onSelectInputClickedHandler} ref={buttonRef}>{selected}</div>
            </div>
        </>
        
    )
}

export default SelectInput;