import React, { useState, useEffect, useRef } from 'react';

import svg from '../../assets/image/sprite.svg';
import classes from './Question.module.scss';
import Timer from '../Timer/Timer';
import RadioButton from '../UI/RadioButton/RadioButton';
import Button from '../UI/Button/Button';
import SuccessCheck from '../SuccessCheck/SuccessCheck';

const Question = (props) => {
    const [selectedChoice, setSelectedChoice] = useState(null);
    const [countDown, setCountDown] = useState(15);

    let timer = useRef(null);

    useEffect(() => {
        setSelectedChoice(null);

        setCountDown(15);

        timer.current = setInterval(() => {
            setCountDown(countDown => countDown - 1);
        }, 1000);

        return () => {
            clearInterval(timer.current);
        }
    }, [props.questionNumber]);

    const answerSelected = () => {
        props.selected(props.questionNumber, selectedChoice, countDown);
        setSelectedChoice(null);

        clearInterval(timer.current);
    }

    const onChoiceMarkedHandler = (index) => {
        setSelectedChoice(props.questionChoices[index]);
    }

    const onJokerUsed = () => {
        props.jokerUsed(props.questionNumber - 1);
    }

    if(countDown <= 0) {
        let gameOverData = {
            header: 'Game Over',
            text: 'Your time ran out!',
            icon: 'hourglass-o'
        }
        props.timeIsUp(gameOverData);

        clearInterval(timer.current);
    }

    return (
        <>
            <div className={classes.Question}>
                <div className={classes.Question__Joker}>
                    <Button clicked={onJokerUsed} disabled={!props.canUseJoker}>50:50</Button>
                </div>

                <Timer time={countDown} />

                <span className={classes.Question__Number}>{`Q${props.questionNumber}`}</span>
                <div className={classes.Question__Text}>
                    {props.questionText}
                </div>

                <div className={classes.Question__Choices}>
                    {props.questionChoices.map((choice, index) => {
                        return <RadioButton 
                            key={`choice${index}`}
                            id={`choice${index}`}
                            label={choice}
                            name={`question${props.questionNumber}`}
                            selected={() => onChoiceMarkedHandler(index)}
                        />
                    })}
                </div>
                
                {props.answered ? <SuccessCheck isSuccess={props.success} /> : null}
                <Button clicked={answerSelected} disabled={!selectedChoice}>
                    <span className={classes.ButtonText}>
                        <svg className={classes.ButtonText__Icon}>
                            <use xlinkHref={`${svg}#icon-check`}></use>
                        </svg>
                        <span>
                            Select
                        </span>
                    </span>
                </Button>
            </div>
        </>
    )
}

export default Question;