import React, { useState, useEffect, useRef } from 'react';

import classes from './App.module.scss';
import StartQuiz from './containers/StartQuiz/StartQuiz';
import Quiz from './containers/Quiz/Quiz';
import Modal from './components/Modal/Modal';
import Title from './components/Title/Title';
import Intro from './components/Intro/Intro';

function App() {
	const [apiUrl, setApiUrl] = useState("");
	const [quizStarted, setQuizStarted] = useState(false);
	const [modalConfig, setModalConfig] = useState({
		show: false,
		header: '',
		text: '',
		score: '',
		icon: null
	});

	let timeout = useRef(null);

	useEffect(() => {
		return () => {
			clearTimeout(timeout.current);
		}
	}, []);

	const onQuizStartedHandler = (url) => {
		setApiUrl(apiUrl => url);

		setQuizStarted(true);
	}

	const onQuizFinishedHandler = (data) => {
		setModalConfig(modalConfig => {
			return {
				show: true,
				header: data.header,
				text: data.text,
				score: data.score,
				icon: data.icon
			}
		});
		
		setQuizStarted(false);
	}

	const onModalClosedHandler = () => {
		timeout.current = setTimeout(() => {
			setModalConfig(modalConfig => {
				return {
					show: false,
					header: '',
					text: '',
					score: '',
					icon: null
				}
			});
		}, 200);
	}

	return (
		<>
			<Modal
				show={modalConfig.show}
				closed={onModalClosedHandler}
				header={modalConfig.header}
				text={modalConfig.text}
				score={modalConfig.score}
				icon={modalConfig.icon}
			/>

			<div className={classes.App}>
				{quizStarted ? <Quiz apiUrl={apiUrl} gameOver={onQuizFinishedHandler} /> : null}

				{!quizStarted ? <>
					<Title title="Queasy" />

					<Intro />

					<StartQuiz quizStarted={onQuizStartedHandler} /> 
				</> : null}
			</div>
		</>
	);
}

export default App;