import React from 'react';

import svg from '../../assets/image/sprite.svg';
import classes from './Intro.module.scss';

const Intro = () => {
    return (
        <div className={classes.Intro}>
            <div className={classes.Intro__Text}>
                <p>
                    A fun trivia game! You can choose your own difficulty &amp; category!
                </p>
                <p>
                    Or not, to make things more interesting!
                </p>
            </div>
            <svg className={classes.Intro__Icon}>
                <use xlinkHref={`${svg}#icon-arrow-down`}></use>
            </svg>
        </div>
    )
}

export default Intro;