import React, { useState } from 'react';

import classes from './StartQuiz.module.scss';
import SelectInput from '../../components/UI/SelectInput/SelectInput';
import Button from '../../components/UI/Button/Button';

const StartQuiz = (props) => {
    const [formControls, setFormControls] = useState({
        difficulty: {
            label: "Difficulty",
            options: ['Any Difficulty', 'Easy', 'Medium', 'Hard'],
            value: 'Any Difficulty'
        },
        category: {
            label: "Category",
            options: [
                'Any Category', 'General Knowledge', 'Entertainment: Books', 'Entertainment: Film',
                'Entertainment: Music', 'Entertainment: Television', 'Entertainment: Video Games',
                'Entertainment: Board Games', 'Science & Nature', 'Science: Computers', 'Science: Mathematics',
                'Mythology', 'Sports', 'Geography', 'History', 'Politics', 'Art', 'Celebrities', 'Animals',
                'Vehicles', 'Entertainment: Comics', 'Science: Gadgets'
            ],
            value: 'Any Category'
        }
    });
    
    let triviaCategories = require('../../assets/triviaCategories.json');

    const obtainCategoryId = (selectedCategory) => {
        let category = triviaCategories.trivia_categories.find(category => category.name ===selectedCategory);

        return category.id;
    }

    const onValueSelectedHandler = (inputName, value) => {
        // Deep copy the state
        const copiedFormControls = {...formControls};

        const copiedFormControl = {...copiedFormControls[inputName]};

        copiedFormControl.value = value;

        copiedFormControls[inputName] = copiedFormControl;

        setFormControls(formControls => copiedFormControls);
    }

    const onStartFormSubmitHandler = (event) => {
        event.preventDefault();

        let baseUrl = 'https://opentdb.com/api.php?amount=10&type=multiple';

        let chosenDifficulty = formControls.difficulty.value;
        let chosenCatagory = formControls.category.value;
        let categoryId;

        if(chosenCatagory !== 'Any Category') {
            // Get the id of the chosen category
            categoryId = obtainCategoryId(chosenCatagory);
        }

        if(categoryId) {
            baseUrl += '&category=' + categoryId;
        }

        if(chosenDifficulty !== 'Any Difficulty') {
            baseUrl += '&difficulty=' + chosenDifficulty.toLowerCase();
        }

        props.quizStarted(baseUrl);
    }

    // const resetFields = () => {

    // }

    let formContent = Object.keys(formControls).map(formCtrl => {
        return <SelectInput
            key={formCtrl}
            label={formControls[formCtrl].label}
            itemList={formControls[formCtrl].options}
            valueSelected={(value) => onValueSelectedHandler(formCtrl, value)} 
        />
    });

    return (
        <section className={classes.StartQuiz}>
            <form className={classes.StartQuiz__Form} onSubmit={onStartFormSubmitHandler}>
                {formContent}

                <Button>Start</Button>
            </form>
        </section>
    );
}

export default StartQuiz;